export const forEach = (selector, callback) => {
    Array.from(document.querySelectorAll(selector)).forEach(element => {
        callback(element);
    });
};
export function ra(array) {
    return array[Math.floor(Math.random() * array.length)];
}
// return random value from weighted array
export function rw(object, save = true) {
    if (object.array) {
        return ra(object.array);
    }
    const array = [];
    for (const key in object) {
        for (let i = 0; i < object[key]; i++) {
            array.push(key);
        }
    }
    if (save) {
        Object.defineProperty(object, "array", {
            enumerable: false,
            configurable: true,
            writable: false,
            value: array
        });
    }
    return ra(array);
}
export function P(probability) {
    return Math.random() < probability;
}
export function camelize(str) {
    return str
        .toLowerCase()
        .replace(/\.[^/.]+$/, "") // remove extension
        .replace(/[()]/g, "") // remove parentheses
        .replace(/[^a-zA-Z0-9]+(.)/g, (m, c) => c.toUpperCase())
        .replace(/^[0-9]/, "_"); // first char should not be a number
}
export const capitalize = (str) => str.charAt(0).toUpperCase() + str.slice(1);
export function link(url, text) {
    return `<a href="${url}" target="_blank">${text}</a>`;
}
export function openURL(url) {
    return window.open(url, "_blank");
}
export const minmax = (value, min, max) => {
    return Math.max(Math.min(Math.round(value), max), min);
};
export const fetcher = (url) => () => {
    return fetch(url).then(result => result.json());
};
